.attendance-table {
  /* width: 50%; */
  max-width: 1920px;
  border-collapse: collapse;
  border: 1px solid #000;
  margin-top: 30px;
}

.attendance-table th, .attendance-table td {
  border: 1px solid #000;
  text-align: center;
  padding: 8px;
  white-space: nowrap; 
  width: 20px;
}

.attendance-table th {
  background-color: white; /* 헤더 배경색 */
}

/* 주말에 회색 배경 적용 */
.weekend {
  background-color: '#d9d9d9';
}

/* 일요일에 빨간색 텍스트 적용 */
.sunday {
  background-color: '#d9d9d9';
  color: red;
}

/* 현재 날짜 스타일 */
.today {
  background-color: #ffecb3; /* 오늘 날짜 배경색 */
}

/* 근무 상태에 따른 배경색 적용 */
.work-on {
  background-color: orange; /* 출근 상태 배경색 */
}

.work-off {
  background-color: orange; /* 퇴근 상태 배경색 */
}

.half-day {
  background-color: orange; /* 반차 배경색 */
}

.day-off {
  background-color: orange; /* 휴가 배경색 */
}
